import SlideButton from "./Slide-Button";
import { Button, Carousel } from "react-bootstrap";
import banners from "../../JSON/data.json";
import { Arrowrightw } from "../../icons";

function MySwiper({ device }) {
  const REGIS_TEXT = "Regístrate";
  const regis = process.env.REACT_APP_REG_URL;

  const dateInBetween = (banner) => {
    return (
        new Date() >= new Date(banner.startDate) &&
        new Date() <= new Date(banner.endDate)
    );
  };

  const BannerFilter = (banner) => {
    if (!banner.scheduled) return banner;
    else if (dateInBetween(banner)) return banner;
  };

  const imgs = !device
      ? banners.desktop_slide_list.filter(BannerFilter)
      : banners.mobile_slide_list.filter(BannerFilter);

  return (
      <>
        <Carousel fade interval="1000" pause="hover" controls={false} touch={true}>
          {imgs.map((item, index) => (
              <Carousel.Item as={"a"} href={regis} key={index}>
                <img
                    {...(item.preload !== 1 ? {} : { loading: "lazy" })}
                    className="banner-img"
                    src={item.image}
                    width={device ? "300" : "400"}
                    height={device ? "410" : "140"}
                    alt={item.img_alt}
                />
                {item.tycLink && <a className="tycTopBanner" href={item.tycLink}>Términos y condiciones</a>}
              </Carousel.Item>
          ))}
        </Carousel>

        {!device ? (
            <div>
              <Button className="central-regis gl-effect" href={regis} rel={"nofollow"}>
                {REGIS_TEXT}
                <Arrowrightw />
              </Button>
            </div>
        ) : (
            <SlideButton regis={regis} />
        )}
      </>
  );
}

export default MySwiper;
