import { Button } from "react-bootstrap";
import { Arrowrightw } from "../../icons";

function TopGame({ banner }) {
 const gameUrl = `https://m.apuestas.codere.es/deportes/#/${banner.gameType}Page?playgame=${encodeURIComponent(banner.iniciator +" "+banner.name)}`;
  //const gameUrl = `https://m.apuestas.codere.es/deportes/#/${banner.gameType}Page?playgame=${banner.iniciator}${banner.name}`
  console.log(gameUrl)
  return (
    <div className="topGame">
      <div className="card">
        <img
          width="16"
          height="5"
          className="game_logo"
          src={banner.logo}
          alt={`${banner.name}-logo`}
        />
        <a href={gameUrl} rel={"nofollow"}>
          <img
            width="18"
            height="10"
            className="topGameImage"
            src={banner.image}
            alt={banner.name}
          />
        </a>
        <div className="game_info">
          <h3 className="topGameTitle">{banner.name}</h3>
          <div className="topGameSponsor">{banner.sponsor}</div>
        </div>
        <div className="jugar-div">
          <Button
            className="jugar-regis rounded-pill d-flex justify-content-around align-items-center"
            size="lg"
            href={gameUrl}
          >
            <span>Juega Ahora</span>
            <div style={{ margin: "2px 0 0 0" }}>
              <Arrowrightw />
            </div>
          </Button>
        </div>
      </div>
    </div>
  );
}

export default TopGame;
