import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, Lazy } from "swiper";

import "swiper/css";
import "swiper/css/lazy";
import "swiper/css/autoplay";

function GameSwiper({ games }) {
  const GAME_PREFIX = "https://m.apuestas.codere.es/deportes/#";
  const IMAGE_PREFIX = "https://cdn.jsdelivr.net/gh/Codere2022/spain-public-cdn@main/img";

  return (
    <div className="game-carousel">
      <Swiper
        modules={[Autoplay, Lazy]}
        lazy={{ loadPrevNext: true, loadPrevNextAmount: 1 }}
        autoplay={{ delay: 2000, disableOnInteraction: false }}
        spaceBetween={15}
        slidesPerView={3}
        loop={true}
      >
        {games.map((item, index) => (

          <SwiperSlide key={index}>

            <a
              href={`${GAME_PREFIX}/${item.gameType}?playgame=${item.sponsor} ${item.name}`}
              rel={"nofollow"}
            >
              <img
                width="50"
                height="50"
                src={`${IMAGE_PREFIX}/${item.sponsor}${item.name.replaceAll(
                  " ",
                  ""
                )}_square.webp`}
                alt={item.alt}
              />
            </a>

          </SwiperSlide>
        ))}
      </Swiper>
    </div>
  );
}
export default GameSwiper;
